/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: "";
  content: none; }

q:before, q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.show__item {
  width: 300px;
  border-radius: 5px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 5px;
  margin: 5px 0px; }
  .show__item--image {
    height: 150px;
    width: auto;
    border-radius: 5px; }
  .show__item--title {
    font-size: 22px;
    margin: 7px;
    text-align: center; }
  .show__item--marked {
    background-color: #f9d143;
    color: #101938; }

.show__fav {
  width: 200px;
  cursor: pointer; }
  .show__fav .show__item--title {
    font-size: 18px; }
  .show__fav .show__item--image {
    height: 100px;
    width: auto;
    border-radius: 5px; }

.btn-search,
.btn-confirm {
  background-color: #f9d143;
  box-sizing: border-box;
  color: #060d17;
  border: none;
  border-radius: 5px;
  font-family: "Fjalla One", sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  margin-left: 20px; }

.btn-reset {
  background-color: #f9d143;
  box-sizing: border-box;
  color: #060d17;
  border: none;
  border-radius: 5px;
  font-family: "Fjalla One", sans-serif;
  font-size: 18px;
  text-transform: uppercase;
  margin-top: 5px;
  width: 100px; }

.close-icon {
  margin-top: 5px;
  color: #f9d143; }

.link {
  text-decoration: none;
  background-color: #f9d143;
  box-sizing: border-box;
  color: #060d17;
  border: none;
  border-radius: 5px;
  font-family: "Fjalla One", sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  margin-left: 20px; }

.page {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 15px;
  background-color: #060d17;
  color: #cdcfd6;
  font-family: "Lato", sans-serif;
  height: 100%; }

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: calc(100vh - 230px); }

.sections {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 15px; }
  @media all and (min-width: 768px) {
    .sections {
      flex-direction: row; } }

.page__header {
  color: #f9d143;
  font-family: "Fjalla One", sans-serif;
  font-size: 50px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px; }

.filter-section {
  text-align: center; }

.form {
  height: 50px;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.fav__section {
  box-sizing: border-box;
  border: #cdcfd6 0.5px solid;
  border-radius: 5px;
  min-height: 50px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center; }
  @media all and (min-width: 768px) {
    .fav__section {
      justify-content: flex-start;
      max-width: 800px; } }

.fav__title {
  font-size: 28px;
  text-transform: uppercase;
  margin-bottom: 10px; }

@media all and (min-width: 768px) {
  .fav__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center; } }

.shows__list {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media all and (min-width: 768px) {
    .shows__list {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center; } }

.hidden {
  display: none; }

.page__footer {
  font-family: "Fjalla One", sans-serif;
  font-size: 12px;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
